___CSS_LOADER_EXPORT___.push([module.id, "\n.digivigilMainDiv{\n  display: block;\n  position: relative;\n\n  text-align: center;\n  color:  #205377;\n  overflow: visible;\n}\n.MainInfoDiv{\n  padding: 3px;\n  padding-top: 10px;\n  font-size: 18pt;\n  position: relative;\n  vertical-align: middle;\n  margin: 0;\n  text-align: left;\n  display: block;\n  background: rgba(255, 255, 255, 0.9);\n  padding: 20px;\n}\n#MainInfoAndLogo {\n  display: block;\n  margin: 0;\n  border: none;\n  padding: 0px;\n\n  padding-top : 10px;\n\n\n\n  margin-bottom: 40px;\n}\n#MainInfoDiv{\n  line-height: 18pt;\n  display: block;\n\n  text-align: left;\n\n  background: rgba(96, 95, 95, 0.3);\n  padding: 20px;\n\n  margin: auto;\n  font-size: 18pt;\n  white-space: normal;\n\n  display: inline-block;\n\n  text-align: center;\n  padding: 20px;\n  width: 100%;\n}\n.MainYouTubeDiv{\n\n  display: inline-block;\n  margin: auto;\n  margin-bottom: 40px;\n  margin-top: 20px;\n}\n.MainYoutubeVideoIframe{\n  display: block;\n  border: solid white 5px;\n  border-radius: 20px;\n  width: 80vw;\n  height: 45vw;\n}\n.MainRouterLink{\n  text-decoration: none;\n  font-weight: bold;\n  color: #205377\n}\n.MainRouterLink:hover{\n  color: #96170d\n}\n#digivigilMainTextLogo{\n  max-width: 30vw;\n}\n.mainLogoDiv {\n  position: relative;\n\n\n  display: inline-block;\n  background: rgba(255, 255, 255, 0.8);\n  margin: auto;\n  padding: 30px;\n  border-radius: 40px;\n  border: solid 5px #205377;\n  border: none;\n  height: auto;\n  vertical-align: top;\n\n  margin-bottom: 30px;\n}\n\n", "",{"version":3,"sources":["webpack://./src/components/about/Main.vue"],"names":[],"mappings":";AAgEA;EACE,cAAc;EACd,kBAAkB;;EAElB,kBAAkB;EAClB,eAAe;EACf,iBAAiB;AAGnB;AAEA;EACE,YAAY;EACZ,iBAAiB;EACjB,eAAe;EACf,kBAAkB;EAClB,sBAAsB;EACtB,SAAS;EACT,gBAAgB;EAChB,cAAc;EACd,oCAAoC;EACpC,aAAa;AACf;AAEA;EACE,cAAc;EACd,SAAS;EACT,YAAY;EACZ,YAAY;;EAEZ,kBAAkB;;;;EAIlB,mBAAmB;AACrB;AACA;EACE,iBAAiB;EACjB,cAAc;;EAEd,gBAAgB;;EAEhB,iCAAiC;EACjC,aAAa;;EAEb,YAAY;EACZ,eAAe;EACf,mBAAmB;;EAEnB,qBAAqB;;EAErB,kBAAkB;EAClB,aAAa;EACb,WAAW;AACb;AAGA;;EAEE,qBAAqB;EACrB,YAAY;EACZ,mBAAmB;EACnB,gBAAgB;AAElB;AAGA;EACE,cAAc;EACd,uBAAuB;EACvB,mBAAmB;EACnB,WAAW;EACX,YAAY;AACd;AACA;EACE,qBAAqB;EACrB,iBAAiB;EACjB;AACF;AAEA;EACE;AACF;AAGA;EACE,eAAe;AAEjB;AAIA;EACE,kBAAkB;;;EAGlB,qBAAqB;EACrB,oCAAoC;EACpC,YAAY;EACZ,aAAa;EACb,mBAAmB;EACnB,yBAAyB;EACzB,YAAY;EACZ,YAAY;EACZ,mBAAmB;;EAEnB,mBAAmB;AAErB","sourcesContent":["<template>\n  <div class=\"digivigilMainDiv\">\n\n    <div class=\"MainYouTubeDiv\">\n      <iframe class=\"MainYoutubeVideoIframe\"  src=\"https://www.youtube.com/embed/XO4WX0AjYLA\" title=\"YouTube video player\" frameborder=\"0\" allow=\"accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture\" allowfullscreen=\"\"></iframe>\n\n    </div>\n\n    <div class=\"MainInfoDiv\">To progress beyond a prototype stage that has been used successfully for internal tasks, <router-link to=\"balek\" class=\"MainRouterLink\">Balek</router-link> and <router-link to=\"diaplode\" class=\"MainRouterLink\">Diaplode</router-link> are transitioning to a release suitable for public adoption.\n      <br/>\n      <br/>\n      To achieve this goal I will be:\n      <ul>\n        <li>Refactoring from Javascript to TypesScript.</li>\n        <li>Updating Documentation</li>\n        <li>Creating Tests</li>\n      </ul>\n\n\n\n      <a target=\"_blank\" class=\"MainLink\" href=\"https://github.com/ephedrandrox/balek/\">The current iterations</a> utilize the Dojo Toolkit to enable design patterns now available from language features. Causing the Dojo toolkit to lose much of it's value in the light of language and package progressions like Typescript and Babel.\n      <br/><br/>\n      As the Typescript conversion occurs, <a class=\"MainLink\" href=\"/docs/\">Documentation</a>, <a class=\"MainLink\" href=\"/docs/jest-stare/\">Tests</a>, and <a class=\"MainLink\" href=\"/docs/coverage/\">Test Coverage</a> reflecting the current state will be available.\n      <br/><br/>The repositories can be found <a target=\"_blank\" class=\"MainLink\" href=\"https://www.github.com/digivigil\">on Github. </a> Where you can\n      ðŸ‘€ watch them if interested in release updates.<br/>\n\n    </div>\n\n\n    <div class=\"MainInfoTitleDiv\" style=\"margin-bottom: 40px; border: none; border-bottom: solid black 4px; text-align: left\">\n      <img class=\"MainInfoTitleLogoImg\" :src=\"balekLogo\" />\n      Use Cases\n    </div>\n    <div class=\"MainYouTubeDiv\">\n      <iframe class=\"MainYoutubeVideoIframe\"  src=\"https://www.youtube.com/embed/n3bwKBiYDTU\" title=\"YouTube video player\" frameborder=\"0\" allow=\"accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture\" allowfullscreen=\"\"></iframe>\n\n    </div>\n\n\n\n  </div>\n</template>\n<script>\nimport digivigilLogo from './resources/digivigil.svg'\nimport DigivigilTextLogo from './resources/DigivigilLogoText.svg'\n\n\nimport BalekInfo from '../balek/Info.vue'\nimport DiaplodeInfo from '../diaplode/Info.vue'\n\nexport default {\n  name: \"Digivigil\",\n  data: function () {\n    return {\n      digivigilLogo: digivigilLogo,\n      DigivigilTextLogo: DigivigilTextLogo,\n    }\n  },\n  components: {\n    BalekInfo,DiaplodeInfo\n  }\n};\n</script>\n<style>\n.digivigilMainDiv{\n  display: block;\n  position: relative;\n\n  text-align: center;\n  color:  #205377;\n  overflow: visible;\n\n\n}\n\n.MainInfoDiv{\n  padding: 3px;\n  padding-top: 10px;\n  font-size: 18pt;\n  position: relative;\n  vertical-align: middle;\n  margin: 0;\n  text-align: left;\n  display: block;\n  background: rgba(255, 255, 255, 0.9);\n  padding: 20px;\n}\n\n#MainInfoAndLogo {\n  display: block;\n  margin: 0;\n  border: none;\n  padding: 0px;\n\n  padding-top : 10px;\n\n\n\n  margin-bottom: 40px;\n}\n#MainInfoDiv{\n  line-height: 18pt;\n  display: block;\n\n  text-align: left;\n\n  background: rgba(96, 95, 95, 0.3);\n  padding: 20px;\n\n  margin: auto;\n  font-size: 18pt;\n  white-space: normal;\n\n  display: inline-block;\n\n  text-align: center;\n  padding: 20px;\n  width: 100%;\n}\n\n\n.MainYouTubeDiv{\n\n  display: inline-block;\n  margin: auto;\n  margin-bottom: 40px;\n  margin-top: 20px;\n\n}\n\n\n.MainYoutubeVideoIframe{\n  display: block;\n  border: solid white 5px;\n  border-radius: 20px;\n  width: 80vw;\n  height: 45vw;\n}\n.MainRouterLink{\n  text-decoration: none;\n  font-weight: bold;\n  color: #205377\n}\n\n.MainRouterLink:hover{\n  color: #96170d\n}\n\n\n#digivigilMainTextLogo{\n  max-width: 30vw;\n\n}\n\n\n\n.mainLogoDiv {\n  position: relative;\n\n\n  display: inline-block;\n  background: rgba(255, 255, 255, 0.8);\n  margin: auto;\n  padding: 30px;\n  border-radius: 40px;\n  border: solid 5px #205377;\n  border: none;\n  height: auto;\n  vertical-align: top;\n\n  margin-bottom: 30px;\n\n}\n\n</style>"],"sourceRoot":""}]);
